.text-input-search {
  font-size: 17px;
  background-color: white;
  border-radius: 30px;
  margin: 4px;
  padding: 12px 40px;
  width: 100%;
  box-shadow: 0px 2px 6px 3px #2e2e2e6a;
  outline: none;
  border: none;
  transition: box-shadow 300ms, padding 300ms;

  &::placeholder {
    transition: opacity 300ms, transform 700ms;
  }

  &:focus::placeholder {
    opacity: 0;
  }
}

.text-input-search-placeholder-transition::placeholder {
  transform: translateX(10px);
  opacity: 0;
}

.text-input-search {
  &:hover {
    box-shadow: 0px 1px 4px 2px #2e2e2e6a;
    padding: 12px 40px;
    transition: box-shadow 500ms, padding 500ms;
  }

  &:focus {
    box-shadow: 0px 0px 6px 4px #65656547;
    padding: 12px 30px;
    transition: box-shadow 500ms, padding 500ms;
  }
}
